import { Link } from "react-router-dom";
import styled from "styled-components";
import { GlobalStyle } from "../css/reset";

const NotFoundWrap = styled.section`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 30px;
  height: 100vh;
  text-align: center;
  h2 {
    font-size: 26px;
    line-height: 1.4;
    word-break: keep-all;
    img {
      display: block;
      width: 150px;
      margin: 0 auto 10px;
    }
  }
  p {
    padding-top: 10px;
    font-size: 20px;
    line-height: 1.3;
  }
  a {
    margin-top: 50px;
    width: 200px;
    font-size: 20px;
    line-height: 50px;
    color: #fff;
    border-radius: 8px;
    background-color: ${(props) => props.theme.colors.ACTIVE};
  }
`;
function NotFound() {
  return (
    <NotFoundWrap>
      <GlobalStyle />
      <h2>
        <img src="/img/warning.png" alt="" />
        요청하신 페이지를 찾을 수 없습니다.
      </h2>
      <p>페이지 주소가 잘못 입력되었거나, 없는 페이지 입니다.</p>
      <Link to="/portfolio">돌아가기</Link>
    </NotFoundWrap>
  );
}

export default NotFound;
